




























































































import Vue from "vue";
import html2canvas from "html2canvas";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import { IWallet } from "@/interfaces";

@Component
export default class Wallet extends Vue {
    @Prop({ required: true }) public wallet: IWallet;
    private isCopying: boolean = false;
    private isSaving: boolean = false;

    get codeForAddress() {
        return JSON.stringify({ address: this.wallet.address });
    }

    get codeForPassphrase() {
        return JSON.stringify({ passphrase: this.wallet.passphrase });
    }

    get passphraseWords() {
        return this.wallet.passphrase.split(" ");
    }

    public print() {
        window.print();
    }

    public copy() {
        const passphrase = document.querySelector("#wallet-passphrase");
        passphrase.setAttribute("type", "text");
        // @ts-ignore
        passphrase.select();
        try {
            document.execCommand("copy");
            // copied
        } catch (err) {
            // not copied
        }
        passphrase.setAttribute("type", "hidden");
        window.getSelection().removeAllRanges();

        this.animate("isCopying");
    }

    public save() {
        html2canvas(document.querySelector("#wallet-details"), {
            x: 150,
            y: 430,
            scrollX: 0,
            scrollY: 0,
            width: 737,
            height: 634,
            windowWidth: 1024,
            windowHeight: 800,
        }).then(canvas => {
            const link = document.createElement("a");
            link.href = canvas.toDataURL("image/jpeg").replace("image/jpeg", "image/octet-stream");
            link.download = "ark-paper-wallet.jpg";
            link.click();

            this.animate("isSaving");
        });
    }

    private animate(key: string): void {
        this[key] = true;
        setTimeout(() => (this[key] = false), 1000);
    }
}
